

import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Vue from 'vue'
import { setInterval } from 'timers';
Vue.use(LoadingPlugin)


export default {
components: {
Group,
XInput,
XButton,
AlertPlugin,
},
data() {
return {
countdown: 5,
errInfo: false,
name: '',
pwd: '',
logoUrl: require('../assets/images/dialog-success.png'),

}
},
methods: {
cfLogin() {

if (true) {
this.$vux.loading.show({
text: '请稍候.....'
})
setTimeout(() => {
this.$router.push({
name: 'activityIndex'
});
this.$vux.loading.hide();
}, 2000);
} else {
this.errInfo = true;
setTimeout(() => {
this.errInfo = false;
}, 3000);




}
}
},
mounted () {
var s = setInterval(() => {
if (0 == this.countdown) {
clearInterval(s);
this.countdown = 5;
this.$router.push({
name: 'activityIndex'
});
} else {
this.countdown = this.countdown -1;
}
}, 1000)
}
}



