
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import Vue from 'vue'
Vue.use(ConfirmPlugin)
export default {
components: {
XSwitch,
XAddress ,
Badge ,
XHeader,
AlertPlugin,
Cell ,
XButton,
},
data() {
return {
value: '',
value2: [],
}
},
methods: {
shit() {
alert(1);
},
rightIconClk() {
this.$vux.alert.show({
title: '提示',
content: '点击右边！',
})
},
leftIconClk() {
this.$vux.alert.show({
title: '提示',
content: '点击左边！',
})
},
logout() {
var _this = this;
this.$vux.confirm.show({
title: '提示',
content: '确定注销吗？',

onCancel () {
console.log(_this) 
},
onConfirm () {
_this.$router.push({
name: 'login'
});
}
})
}
},
mounted() {
this.$store.commit('UPDATE_PAGE_TITLE', '我的')
},
}



